.title-div {
  height: 100vh;
  min-height: 500px;
  overflow: auto;
  text-align: center;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(10%, rgba(74, 17, 141, 0)), to(#061649)),  url('../../pictures/wp10.jpg');
  background-image: -webkit-linear-gradient(rgba(74, 17, 141, 0) 10%, #061649 100%),  url('../../pictures/wp10.jpg');
  background-image: -o-linear-gradient(rgba(74, 17, 141, 0) 10%, #061649 100%),  url('../../pictures/wp10.jpg');
  background-image: linear-gradient(rgba(74, 17, 141, 0) 10%, #061649 100%),  url('../../pictures/wp10.jpg');
  background-position: center center;
  background-size: cover;
}

.content-div {
  min-height: 100vh;
  text-align: center;
  background-image: -webkit-radial-gradient(150% 55%, farthest-side, rgba(6,22,73,1), rgba(6,22,73,1) 70%);
  background-image: -o-radial-gradient(150% 55%, farthest-side, rgba(6,22,73,1), rgba(6,22,73,1) 70%);
  background-image: radial-gradient(farthest-side at 150% 55%, rgba(6,22,73,1), rgba(6,22,73,1) 70%); /*rgba(74, 17, 141, 1)*/
  padding: 0.1px;
}

.subtitle {
  text-align: center;
  font-weight: normal;
  text-shadow: 3px 3px 2px black;
}

.home-title {
  font-size: calc(30px + 2vw);
  color: white;
  margin: 120px 0 40px 0;
  font-weight: normal;
  text-shadow: 4px 4px 6px black;
}

.home-arrow {
  width: 40px;
  height: 50px;
  -webkit-animation: arrow-hover infinite 1.5s linear;
          animation: arrow-hover infinite 1.5s linear;
  position: absolute;
  bottom: 40px;
  left: calc(50% - 20px);
}

.waypoint-line {
  border-top: 1px dashed red;
}

.home-arrow:hover {
  background-color: rgba(78,16,144,0.4);
  -webkit-box-shadow: 0px 0px 64px 0px rgba(78,16,144,1);
          box-shadow: 0px 0px 64px 0px rgba(78,16,144,1);
}

@-webkit-keyframes arrow-hover {
  0% { -webkit-transform: translateY(0px) scale(1.0); transform: translateY(0px) scale(1.0); }
  50% { -webkit-transform: translateY(20px) scale(1.05); transform: translateY(20px) scale(1.05); }
  100% { -webkit-transform: translateY(0px) scale(1.0); transform: translateY(0px) scale(1.0); }
}

@keyframes arrow-hover {
  0% { -webkit-transform: translateY(0px) scale(1.0); transform: translateY(0px) scale(1.0); }
  50% { -webkit-transform: translateY(20px) scale(1.05); transform: translateY(20px) scale(1.05); }
  100% { -webkit-transform: translateY(0px) scale(1.0); transform: translateY(0px) scale(1.0); }
}

.not-visible {
  opacity: 0;
}

@media only screen and (max-width: 920px) {
  .home-title {
    margin: 20px 0 20px 0;
  }
}

@media only screen and (max-width: 460px) {
  .home-title {
    margin: 140px 0 40px 0;
  }

  .subtitle {
    font-size: 24px;
  }
}



